import Messages from '../../util/messages.js'
import Constant from '../../util/constant.js'
import mkbRecord from '../../components/mkb-record/mkb-record.vue'
import {
	getTitleByType
} from '../../util/util.js'
export default {
	components: {
		mkbRecord
	},
	
	data() {
		return {
			list: [],
			type: null,
			pageNo: Constant.page.pageNo,
			pageSize: Constant.page.pageSize,
			loadMore: true,
			loading: false
		}
	},
	onLoad(option) {
		this.type = option.type;
		uni.setNavigationBarTitle({
			title: getTitleByType(Number(option.type))
		});

		this.loadRecord();




	},
	onPullDownRefresh() {
		let that = this;
		uni.stopPullDownRefresh();

		if (this.loadMore) {
			this.pageNo++;
			this.loadRecord();
		} else {
			uni.showToast({
				icon: "none",
				title: Messages.LOADING_FINISH
			})
		}








	},
	onReachBottom() {

		if (this.loadMore) {
			this.pageNo++;
			this.loadRecord();
		} else {
			uni.showToast({
				icon: "none",
				title: Messages.LOADING_FINISH
			})
		}








	},
	methods: {
		

		/**
		 * 加载记录数据信息
		 */
		loadRecord() {
			let that = this;
			uni.showLoading();
			this.loading = true;
			if (that.type !== null) {
				that.$http.getUserRecord({
					type: that.type,
					index: that.pageNo,
					size: that.pageSize
				}).then(res => {
					this.loading = false;
					uni.hideLoading();
					if (res && res.code == 200) {
						if (res.data && res.data.length > 0) {								
								that.list = that.list.concat(res.data);
						} else {
							that.loadMore = false;
						}
					} else {
						console.log(res);
						uni.showToast({
							icon: "none",
							title: res.msg || Messages.FAIL_INFO
						})
					}
				}).catch(err => {
					console.log(err);
					uni.showToast({
						icon: "none",
						title: err.msg || Messages.FAIL_INFO
					})
				})
			}

		},
		
		/**
		 * 跳转案例详情
		 * @param {Object} quoteId
		 */
		toDetail(quoteId){
			this.$openPage({
				name: 'example',
				query: {quoteId}
			})
		}
		
		
		
	}
}
